import React from "react";

import { ImageList, ImageListItem, Grid } from "@mui/material";

const Prototype = () => {
  return (
    <Grid container direction="column" alignItems="center" justify="center">
      <Grid item>
        <ImageList sx={{ width: 1200, height: 600 }} cols={6} rowHeight={164}>
          {itemData.map((item, i) => (
            <ImageListItem key={i}>
              <img
                src={`${item.img}?w=120&h=120&fit=crop&auto=format`}
                // srcSet={`${item.img}?w=120&h=120&fit=crop&auto=format&dpr=2 2x`}
                alt={item.title}
                loading="lazy"
                sx={{
                  width: "50px",
                }}
              />
            </ImageListItem>
          ))}
        </ImageList>
      </Grid>
    </Grid>
  );
};

export default Prototype;

